
import Scroller from 'vux/src/components/scroller/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'

import ResumeDetail from '../../components/ResumeDetail'
export default {
components: {ResumeDetail, Scroller, Swiper, SwiperItem, Flexbox, FlexboxItem, Popup},
data () {
return {
showAction: false,
resumes: [
{
id: '1',
name: '陈大师'
}, {
id: '2',
name: 'god'
}, {
id: '3',
name: 'peter'
}
],
s_w: '100%',
s_ws: '100%',
PID: 1,
swiperIndex: 0
}
},
methods: {
swiperChange (val) {
this.swiperIndex = val
},
getConcats () {
this.showAction = !this.showAction
}
},
mounted () {
let width = document.body.clientWidth

this.s_w = (width * 0.9 / 100) + 'rem'
this.s_ws = (width) * (this.resumes.length) / 100 + 'rem'
this.PID = window.screen.height / window.screen.width
}
}



